.nav-container{
    display: flex;
    width: 100%;
    justify-content: space-between;
    align-items: center;
    background-color: var(--primary-color);
    color: var(--secondary-color);
}

.logo{
    font-size: 2.5rem;
    margin: 1rem;
    color: var(--secondary-color);
    margin-left: 2rem;
}
.logo-link{
    text-decoration: none;
}
.nav-input{
    min-width: 6rem;
    font-size: 2rem;
}
.nav-input{
    width: 800px;
    resize: vertical;
}
.menu-items{
    display: flex;
}
.menu-items .nav-ul{
    margin: 0;
    margin-right: 1rem;
    padding: 0;
    display: flex;
}
.menu-items .list-item{
    list-style: none;
}
.menu-items .list-item-left{
    font-size: 2rem;
    display: flex;
    align-items: center;
    /* cursor: pointer; */
}
.menu-items{
    display: flex;
    align-items: center;
    color: var(--secondary-color);
    cursor: pointer;
}

.menu-items .list-item .menu-link {
    text-decoration: none;
    color: var(--secondary-color);
    font-size: 2rem;
    margin: 1rem;
    display: block;
}

.nav-menu{
    background-color: rgba(221, 160, 221, 0.5);
    width: 20rem;
    height: 100vh;
    display: flex;
    justify-content: center;
    position: fixed;
    top: 0;
    left: -100%;
    transition: 850ms;
}

.nav-menu.active{
    left: 0;
    transition: 350ms;
}

.nav-text{
    display: flex;
    justify-content: flex-start;
    align-items: center;
    padding: 0.5rem 0 0.5rem 1rem;
    list-style: none;
    height: 2rem;
    font-size: 1.5rem;
    
}
.nav-text-link{
    text-decoration: none;
    color: var(--secondary-color);
}